export default function Home() {
  const [selected, setSelected] = useState<number | null>(null);

  const vaults = [
    { image: "/assets/pig.png", apr: "4.0%", yield: "4.2%" },
    { image: "/assets/pig.png", apr: "5.5%", yield: "5.8%" },
    { image: "/assets/pig.png", apr: "3.8%", yield: "4.0%" }
  ];

  return (
    <main className="min-h-screen bg-white p-4 flex flex-col">
      <header className="flex justify-end mb-4">
        <button onClick={() => setSelected(null)} className="p-2">
          {selected !== null ? <X className="w-6 h-6" /> : <Settings className="w-6 h-6" />}
        </button>
      </header>

      <div className="flex-1 flex items-center justify-center">
        <div className="flex items-end gap-2 w-full max-w-md">
          {vaults.map((vault, i) => {
            const isSelected = selected === i;
            const isHidden = selected !== null && !isSelected;

            return (
              <div
                key={i}
                onClick={() => !isSelected && setSelected(i)}
                className={`
                  flex flex-col items-center transition-all duration-500
                  ${isSelected ? "flex-[12]" : "flex-1"}
                  ${isHidden ? "flex-[0] opacity-0" : "opacity-100"}
                  ${i === 1 && selected === null ? "-translate-y-8" : ""}
                `}
              >
                {!isSelected && (
                  <div className="text-xs font-bold mb-2">{vault.apr} APR</div>
                )}

                <div className={`bg-white w-full flex flex-col items-center justify-center transition-all duration-500 ${isSelected ? "py-8" : "aspect-square"}`}>
                  {!isSelected ? (
                    <Image src={vault.image} alt="Vault" width={100} height={100} />
                  ) : (
                    <div className="w-full space-y-6">
                      <div className="text-center">
                        <p className="text-xs font-bold text-gray-400 mb-1">YIELD</p>
                        <h2 className="text-6xl font-black">{vault.yield}</h2>
                      </div>
                      
                      <div className="space-y-2">
                        <button className="w-full bg-black text-white py-5 rounded-2xl font-bold active:scale-95 transition-transform">
                          DEPOSIT
                        </button>
                        <button className="w-full bg-white text-black py-4 rounded-2xl font-bold active:scale-95 transition-transform">
                          WITHDRAW
                        </button>
                      </div>
                    </div>
                  )}
                </div>

                {isSelected && (
                  <p className="font-bold text-xl mt-4">VAULT 0{i + 1}</p>
                )}
              </div>
            );
          })}
        </div>
      </div>
    </main>
  );
}